<template>
  <div>
    <b-card-group deck>
      <b-card
        header-tag="header"
        title="Create Employee"
      >
        <validation-observer ref="simpleRules">
          <b-form>
            <b-row>
              <!-- username -->
              <b-col cols="6">
                <b-form-group
                  label="First Name"
                  label-for="firstName"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="name"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="coupon.firstName"
                      :state="errors.length > 0 ? false:null"
                      placeholder="first name"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- username -->
              <b-col cols="6">
                <b-form-group
                  label="Last Name"
                  label-for="lastName"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="lastName"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="coupon.lastName"
                      :state="errors.length > 0 ? false:null"
                      placeholder="last name"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- username -->
              <b-col cols="6">
                <b-form-group
                  label="Email"
                  label-for="email"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="email"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="coupon.email"
                      type="email"
                      :state="errors.length > 0 ? false:null"
                      placeholder="email"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- username -->
              <b-col cols="6">
                <b-form-group
                  label="Phone"
                  label-for="phone"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="phone"
                    rules="required"
                  >
                    <b-form-input
                      id="phone"
                      v-model="coupon.phone"
                      :state="errors.length > 0 ? false:null"
                      placeholder="phone"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>
              <!-- username -->
              <b-col cols="6">
                <b-form-group
                  label="Started At"
                  label-for="startedAt"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="startedAt"
                    rules="required"
                  >
                    <b-form-input
                      id="name"
                      v-model="coupon.startedAt"
                      type="date"
                      :state="errors.length > 0 ? false:null"
                      placeholder="started At"
                    />
                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <b-col md="6">
                <b-form-group
                  label="Company"
                  label-for="company"
                >
                  <validation-provider
                    #default="{ errors }"
                    name="type"
                    rules="required"
                  >
                    <b-form-select
                      id="country"
                      v-model="coupon.companyId"
                      placeholder="Select Company"
                      value-field="id"
                      text-field="name"
                      :options="companies"
                      :state="errors.length > 0 ? false : null"
                      @change="companyId"
                    />

                    <small class="text-danger">{{ errors[0] }}</small>
                  </validation-provider>
                </b-form-group>
              </b-col>

              <!-- login button -->
              <b-col cols="12">
                <b-button
                  variant="primary"
                  type="submit"
                  @click.prevent="validationForm"
                >
                  Add New Employee
                </b-button>
              </b-col>
            </b-row>
          </b-form>
        </validation-observer>
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
import { localize, ValidationObserver, ValidationProvider } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
// import { required } from '@validations'
import {
  BButton, BCard, BCardGroup, BCol, BForm, BFormGroup, BFormInput, BFormSelect, BRow,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: 'اسم الكوبون',
      percentage: 'نسبة الخصم',
      start: 'تاريخ البدء',
      end: 'تاريخ الانتهاء',
      code: 'الكود',
    },
  },
}
localize('ar', dictionary.ar)
export default {
  name: 'AddCompany',
  components: {
    BFormSelect,
    // BFormFile,
    ValidationProvider,
    ValidationObserver,
    BCard,
    BCardGroup,
    BButton,
    BFormInput,
    BFormGroup,
    BForm,
    BRow,
    BCol,
  },
  data() {
    return {
      companyId: '',
      companies: [],
      required,
      coupon: {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        startedAt: '',
        companyId: '',
      },
    }
  },
  created() {
    this.$store.dispatch('company/fetchName').then(res => {
      this.companies = res.data.data
    })
  },
  methods: {
    generate() {
      const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      let result = ''
      // eslint-disable-next-line no-plusplus
      for (let i = 0; i < 2; ++i) {
        result += alphabet[Math.floor(alphabet.length * Math.random())]
      }
      this.coupon.code = `${result.toLocaleUpperCase()}-${Math.floor(1000 + Math.random() * 9000)}`
    },
    validationForm() {
      const formData = new FormData()
      formData.append('first_name', this.coupon.firstName)
      formData.append('last_name', this.coupon.lastName)
      formData.append('email', this.coupon.email)
      formData.append('phone', this.coupon.phone)
      formData.append('started_at', this.coupon.startedAt)
      formData.append('company_id', this.coupon.companyId)
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          this.$store.dispatch('employee/store', formData).then(() => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'An employee has been added successfully',
                icon: 'CheckCircleIcon',
                variant: 'success',
              },
            })
          })
        }
      })
    },
  },
}
</script>

<style scoped></style>
